import React from "react";
import {
    CardContent,
    Switch,
    Box,
    Button,
    Typography,
    TextField,
} from "@mui/material";
import Card from "@mui/material/Card";
import CardHeader from "@mui/material/CardHeader";
import useZebra from "../../hooks/useZebra";
import useLocalStorage from "../../hooks/useLocalStorage";

/**
 * This component is only active if zebra-printing (command: /zebra) is activated.
 *
 * Functionalities:
 * - Choose the printer you want to connect to the app
 * - Print the configuration label on the printer
 * - Print a test label on the printer
 * - Refresh the printer setup
 */
function SettingsZebra(props) {
    const {
        badgeHeight,
        badgeWidth,
        setBadgeHeight,
        setBadgeWidth,
        printLabel,
    } = useZebra();

    const { value: zebraActivated, setValue: setZebraActivated } =
        useLocalStorage("zebraActivated", undefined);

    function saveInput(e) {
        localStorage.setItem(e.target.id, e.target.value);
        if (e.target.id === "badgeWidth") {
            setBadgeWidth(e.target.value);
        } else if (e.target.id === "badgeHeight") {
            setBadgeHeight(e.target.value);
        }
    }

    return (
        <Card variant="outlined">
            <CardHeader
                action={
                    <Switch
                        checked={zebraActivated}
                        onChange={(e) => {
                            setZebraActivated(e.target.checked);
                        }}
                    />
                }
                title="Zebra Drucker"
            />
            <CardContent>
                <Typography
                    sx={{
                        mb: 2,
                    }}>
                    Der Zebra Drucker muss ber die Zebra Browser Print App mit
                    dem Gert verbunden sein, damit er in SCANdoo verwendet
                    werden kann.
                </Typography>

                <TextField
                    value={""}
                    onChange={(e) => console.log(e)}
                    id="zplInput"
                    label="ZPL Input"
                    variant="outlined"
                    fullWidth
                    sx={{
                        my: 2,
                    }}
                />

                <Box
                    sx={{
                        display: {
                            xs: "inline",
                            sm: "flex",
                            lg: "flex",
                        },
                        justifyContent: "space-between",
                        flexWrap: "wrap",
                    }}>
                    <TextField
                        value={badgeWidth}
                        sx={{ my: 2, mr: 2, width: 150 }}
                        onChange={(e) => saveInput(e)}
                        id="badgeWidth"
                        label="Badge Breite"
                    />
                    <TextField
                        value={badgeHeight}
                        sx={{ my: 2, width: 150 }}
                        onChange={(e) => saveInput(e)}
                        id="badgeHeight"
                        label="Badge Hhe"
                        variant="outlined"
                    />
                </Box>

                <Box
                    sx={{
                        display: {
                            xs: "inline",
                            sm: "flex",
                            lg: "flex",
                        },
                        justifyContent: "center",
                        flexWrap: "wrap",
                        my: 3,
                    }}>
                    <Button
                        disabled={zebraActivated ? "" : "true"}
                        onClick={() =>
                            printLabel(
                                "https://pub-cdn.apitemplate.io/2023/04/993f4297-d641-4838-8792-c5af24bf9ba0.jpeg",
                                "test"
                            )
                        }
                        variant="outlined"
                        color="primary"
                        sx={{
                            m: 1,
                        }}>
                        Testlabel drucken
                    </Button>
                </Box>
            </CardContent>
        </Card>
    );
}

export default SettingsZebra;
