import React from "react";
import QrCodeScannerIcon from "@mui/icons-material/QrCodeScanner";
import PersonSearchIcon from "@mui/icons-material/PersonSearch";
import PrintIcon from "@mui/icons-material/Print";
import HowToRegIcon from "@mui/icons-material/HowToReg";
import SettingsBrightnessIcon from "@mui/icons-material/SettingsBrightness";
import SettingsSlider from "./SettingsSlider";
import useLocalStorage from "../../hooks/useLocalStorage";
import configData from "../../config.json";

import { Grid, Box, TextField, Typography } from "@mui/material";

import BaseCard from "../../components/BaseCard/BaseCard";
import SettingsZebra from "./SettingsZebra";
import { useOutletContext } from "react-router-dom";

export default function Settings(props) {
    const [webhookUrl, setWebhookUrl] = React.useState(
        localStorage.getItem("webhookURL") || ""
    );
    const [eid, setEid] = React.useState(localStorage.getItem("eid") || "");

    const { value: qrCodeActivated, setValue: setQrCodeActivated } =
        useLocalStorage("qrCodeActivated", false);
    const { searchActivated, setValue: setSearchActivated } = useLocalStorage(
        "searchActivated",
        false
    );
    const {
        autoCheckinActivated,
        setAutoCheckinActivated,
        autoBadgePrintActivated,
        setAutoBadgePrintActivated,
    } = useOutletContext();

    const { value: darkModeAcitvated, setValue: setDarkModeAcitvated } =
        useLocalStorage("darkModeAcitvated", false);

    function saveInput(e) {
        localStorage.setItem(e.target.id, e.target.value);
        if (e.target.id === "webhookURL") {
            setWebhookUrl(e.target.value);
        } else if (e.target.id === "eid") {
            setEid(e.target.value);
        }
    }

    const slider = [
        <SettingsSlider
            key="qrCodeActivated"
            value={qrCodeActivated}
            setValue={setQrCodeActivated}
            data={configData.settings.slider.QrCodeActivated}
            icon={<QrCodeScannerIcon />}
        />,
        <SettingsSlider
            key="searchActivated"
            value={searchActivated}
            setValue={setSearchActivated}
            data={configData.settings.slider.searchActivated}
            icon={<PersonSearchIcon />}
        />,
        <SettingsSlider
            key="autoCheckinActivated"
            value={autoCheckinActivated}
            setValue={setAutoCheckinActivated}
            data={configData.settings.slider.autoCheckinActivated}
            icon={<HowToRegIcon />}
        />,
        <SettingsSlider
            key="autoBadgePrintActivated"
            value={autoBadgePrintActivated}
            setValue={setAutoBadgePrintActivated}
            data={configData.settings.slider.autoBadgePrintActivated}
            icon={<PrintIcon />}
        />,
        <SettingsSlider
            key="darkModeAcitvated"
            value={darkModeAcitvated}
            setValue={setDarkModeAcitvated}
            data={configData.settings.slider.darkModeAcitvated}
            icon={<SettingsBrightnessIcon />}
        />,
    ];

    return (
        <Box>
            <Grid container spacing={3}>
                {/* ------------------------- row 1 ------------------------- */}
                <Grid
                    item
                    xs={12}
                    lg={6}
                    sm={6}
                    sx={{
                        display: "flex",
                        alignItems: "stretch",
                    }}>
                    <BaseCard title="Verbindungseinstellungen">
                        <Typography
                            sx={{
                                mb: 2,
                            }}>
                            Der Verbindungsschlssel ist notwendig, um Tickets
                            einchecken zu knnen. Die Event-ID sind fr den
                            Checkin optional, wird aber fr die Statistik
                            gebraucht.
                        </Typography>

                        <TextField
                            value={webhookUrl}
                            onChange={(e) => saveInput(e)}
                            id="webhookURL"
                            label="Verbindungsschlssel"
                            variant="outlined"
                            fullWidth
                            sx={{
                                mb: 2,
                            }}
                        />

                        <TextField
                            value={eid}
                            onChange={(e) => saveInput(e)}
                            id="eid"
                            label="Event ID"
                            variant="outlined"
                            fullWidth
                            sx={{
                                mb: 2,
                            }}
                        />

                        <TextField
                            onChange={(e) => saveInput(e)}
                            id="gate"
                            label="Eingang"
                            variant="outlined"
                            fullWidth
                            sx={{
                                mb: 2,
                            }}
                        />
                    </BaseCard>
                </Grid>

                {/* ------------------------- row 1 ------------------------- */}
                <Grid
                    item
                    xs={12}
                    lg={6}
                    sm={6}
                    sx={{
                        display: "flex",
                        alignItems: "stretch",
                    }}>
                    <SettingsZebra />
                </Grid>
                {slider}
            </Grid>
        </Box>
    );
}
