import useLocalStorage from "./useLocalStorage";
import axios from "axios";

function useZebra() {
    const { value: badgeHeight, setValue: setBadgeHeight } = useLocalStorage(
        "badgeHeight",
        undefined
    );
    const { value: badgeWidth, setValue: setBadgeWidth } = useLocalStorage(
        "badgeWidth",
        undefined
    );

    function printLabel(url, ticketHash) {
        var options = {};
        if (badgeHeight && badgeWidth) {
            options = {
                action: "print",
                fitTo: { width: badgeWidth, height: badgeHeight },
            };
        }

        window.BrowserPrint.getDefaultDevice(
            "printer",
            function (device) {
                console.log("Found default device");
                device.convertAndSendFile(
                    url,
                    () => setDefaultBadgePrinted(ticketHash),
                    () => console.log("error"),
                    options
                );
            },
            function (error) {
                alert(error);
            }
        );
    }

    function printZPLCommand(input) {
        console.log("print ZPL command");
    }

    function setDefaultBadgePrinted(ticketHash) {
        if (ticketHash !== "test") {
            console.log("Default Badge Printed");
            const baseURL =
                "https://hook.doo.integromat.celonis.com/" +
                localStorage.getItem("webhookURL");
            axios
                .get(
                    `${baseURL}?request=defaultBadgePrinted&hash=${ticketHash}`
                )
                .then((response) => console.log(response))
                .catch((err) => console.log(err));
        }
    }

    return {
        badgeHeight,
        badgeWidth,
        setBadgeHeight,
        setBadgeWidth,
        printZPLCommand,
        printLabel,
    };
}

export default useZebra;
